@import url(https://fonts.googleapis/com/css?family=Work+Sans);
body {
  margin: 0;
  font-family: 'Work Sans';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eceff1;
  color: #4d4d4d;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Pokecard {
	/*border: 2px solid mediumaquamarine;
	*/width: 300px;
	padding: 10px;
	margin: 1rem;
	box-shadow: 7px 10px 12px -5px (0.0.0.0.56);
	text-align: center;
	background-color: #fefefe;
	border-radius: 3px;
	-webkit-border-radius: 3px;
	-moz-border-radius: 3px;
	-ms-border-radius: 3px;
	-o-border-radius: 3px;
}

.Pokecard-data {
	 font-size: 80%;
}

.Pokecard-title {
	margin-bottom: 1rem;
	font-weight: bold;
	font-size: 1.5rem;
}

.Pokecard-image {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 250px;
	background-color: #eceff1;
	border-radius: 3px;
	-webkit-border-radius: 3px;
	-moz-border-radius: 3px;
	-ms-border-radius: 3px;
	-o-border-radius: 3px;
	margin-bottom: 1rem; 
}

.Pokecard img {
	transition: all ease 500;
	-webkit-transition: all ease 500;
	-moz-transition: all ease 500;
	-ms-transition: all ease 500;
	-o-transition: all ease 500;
}

.Pokecard img:hover {
	transform: scale(1.25);
	-webkit-transform: scale(1.25);
	-moz-transform: scale(1.25);
	-ms-transform: scale(1.25);
	-o-transform: scale(1.25);
	opacity: 0.7;
}
.Pokedex {
	text-align: center;
}

.Pokedex-cards {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap-reverse;
}

.Pokedex-winner {
	color: #4caf50;
}

.Pokedex-loser {
	color: #e91e63;
}

